<template id="CouncelPage">
  <v-ons-page>
      <v-ons-row>
        <div style="margin-left:auto">
          <v-ons-card style="background-color: #DDFFDD">
            .
          </v-ons-card>
        </div>
      </v-ons-row>
      <v-ons-row>
        <div style="margin-left:auto">
          <v-ons-card style="background-color: #DDFFDD">
                 ,    
                .
          </v-ons-card>
        </div>
      </v-ons-row>
      <v-ons-row>
        <div style="margin-left:auto">
          <v-ons-card style="background-color: #DDFFDD">
             ...
          </v-ons-card>
        </div>
      </v-ons-row>
      
      <v-ons-row>
        <div>
          <v-ons-card>
                 ,  
                     .
          </v-ons-card>
        </div>
      </v-ons-row>

      <v-ons-row>
        <div style="margin-left:auto">
          <v-ons-card style="background-color: #DDFFDD">
             !
          </v-ons-card>
        </div>
      </v-ons-row>

      <v-ons-list style="height: 100%">
        <v-ons-row v-for="item in messageList" :key="item.message">
          <div>
            <v-ons-card>
              {{item.message}}
            </v-ons-card>
          </div>
        </v-ons-row>
      </v-ons-list>
      
      <v-ons-bottom-toolbar>
        <v-ons-row class="myBottom">
          <v-ons-col width="78%">
            <div class="left">
              <v-ons-input placeholder="Input your message"
                v-model="inputMessage" width="50%">
              </v-ons-input>
            </div>
          </v-ons-col>

          <v-ons-col width="15%">
            <div class="right">
              <v-ons-toolbar-button @click="sendMessage(inputMessage, true)"> <!--@click="$emit('toggleMenu')">-->
                <v-ons-icon icon="ion-arrow-up-a"></v-ons-icon>
              </v-ons-toolbar-button>
            </div>
          </v-ons-col>
        </v-ons-row>
      </v-ons-bottom-toolbar>
  </v-ons-page>
</template>

<script>
export default {
  name: 'CouncelPage',
  data () {
    return {
      inputMessage: "",
      messageList: [
        /*
        {
          type: true, // true : me
          message: "",
        }
        */
      ],
      constUpperTag: "",
    }
  },
  methods: {
    sendMessage(msg, tp) {
      this.messageList.push({
        type: tp,
        message: this.inputMessage,
      });
      this.inputMessage = "";
      console.log(this.messageList);
    }
  },
  mounted() {
    setTimeout(() => {
      this.messageList.push({
        type: false,
        message: "     ,            .",
      });
      console.log(this.messageList)
    }, 3000);
  },
};
</script>

<style>
.myBottom {
  margin: 7px 10px 5px 20px;
}
</style>


